<template>
<div>
    <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <!--  -->
            <el-row>
                <el-col>
                    <el-button type="primary"></el-button>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="rolesList" :border="true" :stripe="true">
                <!--  -->
                <el-table-column type="expand">
                    <template slot-scope="scope">
                        <el-row :class="['bdbottom',i1 === 0?'bdtop':'','vcenter']" :key="item1.id" v-for="(item1, i1) in scope.row.children">
                            <!--  -->
                            <el-col :span="5" >
                                <el-tag closable @close='removeRoleByID(scope.row,item1.id)'>{{item1.authName}}</el-tag>
                                <i class="el-icon-caret-right"></i>
                            </el-col>
                            <!--  -->
                            <el-col :span="19">
                                <!-- for -->
                                <el-row :class="[i2 === 0?'':'bdtop','vcenter']" v-for="(item2,i2) in item1.children" :key="item2.id">
                                    <el-col :span="6">
                                        <el-tag type="success" closable @close='removeRoleByID(scope.row,item2.id)'>{{item2.authName}}</el-tag>
                                        <i class="el-icon-caret-right"></i>
                                    </el-col>
                                    <el-col :span="18">
                                        <el-tag type="warning" v-for="(item3) in item2.children" :key="item3.id" closable @close='removeRoleByID(scope.row,item3.id)'>{{item3.authName}}</el-tag>
                                    </el-col>
                                </el-row>
                            </el-col>
                        </el-row>
                    </template>
                </el-table-column>
                <!--  -->
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column label="" prop="roleName"></el-table-column>
                <el-table-column label="" prop="roleDesc"></el-table-column>
                <el-table-column label="" width="300px">
                    <template slot-scope="scope">
                        <el-button size="mini" type="primary" icon="el-icon-edit"></el-button>
                        <el-button size="mini" type="danger" icon="el-icon-delete"></el-button>
                        <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <!--  -->
        <el-dialog
            title=""
            :visible.sync="setRightDialogVisible"
            width="50%" @close='setRightDialogClosed'>
            <!-- <span></span> -->
            <!--  -->
            <el-tree :data="rightsList" :props="treeProps" ref="treeRef" show-checkbox node-key='id' default-expand-all :default-checked-keys='defKeys'></el-tree>

            <span slot="footer" class="dialog-footer">
                <el-button @click="setRightDialogVisible = false"> </el-button>
                <el-button type="primary" @click="allotRights"> </el-button>
            </span>
        </el-dialog>
</div>
</template>

<script>
export default {
  data () {
    return {
    // 
      rolesList: [],
      //   
      setRightDialogVisible: false,
      // 
      rightsList: [],
      //   
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      //   
      defKeys: [],

      roleID: ''
    }
  },
  created () {
    this.getRolesList()
  },
  methods: {
    async getRolesList () {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error('')
      console.log(res)
      this.rolesList = res.data
    },
    async removeRoleByID (role, id) {
    // 
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') return this.$message.info('')
      // console.log('')
      const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${id}`)
      if (res.meta.status !== 200) return this.$message.error('')
      //   
      role.children = res.data
    },
    async showSetRightDialog (role) {
      this.roleID = role.id
      // 
      const { data: res } = await this.$http.get('rights/' + 'tree')
      if (res.meta.status !== 200) return this.$message.error('')
      //  data
      this.rightsList = res.data
      console.log(this.rightsList)
      this.getLeafKeys(role, this.defKeys)
      // 
      this.setRightDialogVisible = true
    },
    // ID
    getLeafKeys (node, arr) {
      if (!node.children) {
        // nodechildren
        return arr.push(node.id)
      }
      node.children.forEach(item => {
        this.getLeafKeys(item, arr)
      })
    },
    // 
    setRightDialogClosed () {
      this.defKeys = []
    },
    // 
    async allotRights (roleID) {
      const keysArr = [...this.$refs.treeRef.getCheckedKeys(), ...this.$refs.treeRef.getHalfCheckedKeys()]
      const keyStr = keysArr.join(',')
      // console.log(keyStr)
      const { data: res } = await this.$http.post(`roles/${this.roleID}/rights`, {
        rids: keyStr
      }).catch(err => err)
      if (res.meta.status !== 200) return this.$message.error('')
      console.log(res.data)
      this.$message.success('')
      // 
      this.getRolesList()
      this.setRightDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-table {
    margin-top: 15px;
}
.el-tag {
    margin: 7px;
}
.bdtop {
    border-top: 1px solid #eee;
}
.bdbottom {
    border-bottom: 1px solid #eee;
}
.vcenter {
    display: flex;
    align-items: center;
}
</style>
