<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <!--  -->
            <el-row>
                <el-row>
                    <el-button type="primary" @click="addCate"></el-button>
                </el-row>
            </el-row>
            <!--  -->
            <tree-table class="treeTable" :data='cateslist' :columns='columns' :selection-type='false' :expand-type='false' show-index index-text='#' border>
              <!--  -->
              <template slot="isok" slot-scope="scope">
                  <i class="el-icon-success" v-if="scope.row.cat_deleted === false" style="color: lightgreen"></i>
                  <i class="el-icon-error" v-else style="color: red"></i>
              </template>
              <!--  -->
              <template slot="order" slot-scope="scope">
                  <el-tag size="mini" v-if="scope.row.cat_level === 0">1</el-tag>
                  <el-tag size="mini" type="success" v-else-if="scope.row.cat_level === 1">2</el-tag>
                  <el-tag size="mini" type="warning" v-else>3</el-tag>
              </template>
              <!--  -->
            <template slot="opt">
                <el-button type="primary" size="mini" icon="el-icon-edit"></el-button>
                <el-button type="danger" size="mini" icon="el-icon-delete"></el-button>
            </template>
            </tree-table>
            <!--  -->
            <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryinfo.pagenum"
            :page-sizes="[3, 5, 10, 15]"
            :page-size="queryinfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
            </el-pagination>
        </el-card>
        <!--  -->
        <el-dialog
        title=""
        :visible.sync="addCateDialogVisible"
        width="50%" @close="addCateDialogClosed">
        <!--  -->
        <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-width="100px">
            <el-form-item label="" prop="cat_name">
                <el-input v-model="addCateForm.cat_name"></el-input>
            </el-form-item>
             <el-form-item label="">
                <el-cascader
                v-model="selectedKeys"
                :options="parentCateList"
                :props="{ expandTrigger: 'hover', checkStrictly: true, ...cascaderProps }"
                @change="paranetCateChanged" clearable>
                </el-cascader>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="addCateDialogVisible = false"> </el-button>
            <el-button type="primary" @click="addCateCommit"> </el-button>
        </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      cateslist: [],
      queryinfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 
      total: 0,
      //   table
      columns: [{
        label: '',
        prop: 'cat_name'
      }, {
        label: '',
        // 
        type: 'template',
        // 
        template: 'isok'
      }, {
        label: '',
        // 
        type: 'template',
        // 
        template: 'order'
      }, {
        label: '',
        // 
        type: 'template',
        // 
        template: 'opt'
      }],
      //   
      addCateDialogVisible: false,
      // model
      addCateForm: {
        cat_name: '',
        // ID
        cat_pid: 0,
        // 
        cat_level: 0
      },
      //  
      addCateFormRules: {
        cat_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      //   
      parentCateList: [],
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      selectedKeys: []
    }
  },
  created () {
    // 
    this.getCatesList()
  },
  methods: {
    async getCatesList () {
    //   15
      const { data: res } = await this.$http.get('categories', { params: this.queryinfo })
      if (res.meta.status !== 200) return this.$message.error('')
      //   
      this.cateslist = res.data.result
      //   
      this.total = res.data.total
      console.log(this.cateslist)
    },
    //   pageISezchange
    handleSizeChange (newSize) {
      console.log('' + newSize)
      this.queryinfo.pagesize = newSize
      this.getCatesList()
    },
    // pagenow
    handleCurrentChange (newPage) {
      console.log('' + newPage)
      this.queryinfo.pagenum = newPage
      this.getCatesList()
    },
    // 
    addCate () {
    // 
      this.getParentCatelist()
      this.addCateDialogVisible = true
    },
    // 
    async getParentCatelist () {
      const { data: res } = await this.$http.get('categories', { params: { type: 2 } })
      if (res.meta.status !== 200) return this.$message.error('')
      // 
      this.parentCateList = res.data // parentCateList
      console.log(res.data)
    },
    paranetCateChanged () {
      console.log(this.selectedKeys)
      //   selectedKeys.length>0
      if (this.selectedKeys.length > 0) {
        // ID
        this.addCateForm.cat_pid = parseInt(this.selectedKeys[this.selectedKeys.length - 1])
        // 
        this.addCateForm.cat_level = this.selectedKeys.length
      } else {
        //   ID
        this.addCateForm.cat_pid = 0
        // 
        this.addCateForm.cat_level = 0
      }
    },
    // 
    addCateCommit () {
      console.log(this.addCateForm)
      // 
      this.addCateDialogVisible = false
    },
    // 
    addCateDialogClosed () {
      // 
      this.$refs.addCateFormRef.resetFields()
      // 
      this.selectedKeys = []
      this.addCateForm.cat_pid = 0
      this.addCateForm.cat_level = 0
    }
  }
}
</script>

<style lang="less" scoped>
.treeTable {
    margin-top: 10px;
    margin-bottom: 15px;
}
.el-cascader {
    height: 100px !important;
}
.el-cascader {
    width: 100%;
}
</style>
