            <el-timeline>
                <el-timeline-item
                v-for="(activity, index) in progressList"
                :key="index"
                :timestamp="activity.time">
                {{activity.context}}
                </el-timeline-item>
            </el-timeline>
        </el-dialog>
    </div>
