<template>
    <div>
      <!--  -->
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <!--  -->
      <el-card>
        <!--  -->
        <el-alert
          title=""
          type="info"
          center
          show-icon
          :closable="false"
          >
        </el-alert>
        <!--  -->
        <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
          <el-step title=""></el-step>
        </el-steps>
        <!-- tab -->
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px" class="demo-ruleForm" label-position='top'>
          <el-tabs v-model="activeIndex" :tab-position="'left'" :before-leave='beforeTabLeave' @tab-click='tabSelected'>
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weigh">
              <el-input v-model="addForm.goods_weigh" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
              v-model="addForm.goods_cat"
              :options="catesList"
              :props="{ expandTrigger: 'hover', ...cateProps }"
              @change="handleChange"></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <el-form-item :key="item.attr_id" :label="item.attr_name" v-for="(item) in manyTableData">
              <!--  -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :key="index" :label="cb" v-for="(cb, index) in item.attr_vals" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item :key="item.attr_id" :label="item.attr_name" v-for="(item) in onlyTableData">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <el-upload
              action="http://120.27.247.79:8888/api/private/v1/upload"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture" :headers='headersObj' :on-success='handleSuccess'>
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <!--  -->
            <quill-editor v-model="addForm.goods_introduce"></quill-editor>
            <el-button type="primary" class="btnAdd" @click="add"></el-button>
          </el-tab-pane>
        </el-tabs>
        </el-form>
      </el-card>
      <!--  -->
      <el-dialog
        title=""
        :visible.sync="previewVisible"
        width="50%"
        :before-close="handleClose">
          <img :src="previewPath" alt="" class="previewImg">
      </el-dialog>
    </div>
</template>

<script>
import _ from 'lodash'
export default {
  data () {
    return {
      activeIndex: '0',
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_weigh: 0,
        goods_number: 0,
        goods_cat: [],
        pics: [],
        goods_introduce: '',
        attrs: []

      },
      addFormRules: {
        goods_name: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        goods_price: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        goods_weigh: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        goods_number: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        goods_cat: [{
          required: true,
          message: '',
          trigger: 'blur'
        }]

      },
      // item
      selectedOptions: [],
      catesList: [],
      cateProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      // 
      manyTableData: [],
      onlyTableData: [],
      uploadURL: 'http://120.27.247.79:8888/api/private/v1/upload',
      // 
      headersObj: { Authorization: window.sessionStorage.getItem('token') },
      // url
      previewPath: '',
      // 
      previewVisible: false
    }
  },
  created () {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.catesList = res.data
      console.log(this.catesList)
    },
    // 
    handleChange () {
      console.log(this.addForm.goods_cat)
      if (this.addForm.goods_cat.length !== 3) this.addForm.goods_cat = []
    },
    beforeTabLeave (activeName, oldActiveName) {
      // console.log(activeName + oldActiveName)
      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('')
        return false
      }
    },
    async tabSelected () {
      // console.log(this.activeIndex)
      if (this.activeIndex === '1') {
        // 
        const { data: res } = await this.$http.get('categories/73/attributes', { params: { sel: 'many' } })
        if (res.meta.status !== 200) return this.$message.error('')
        // key
        res.data.forEach(item => {
          item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split('')
        })
        this.manyTableData = res.data
        console.log('', res.data)
      } else if (this.activeIndex === '2') {
        // 
        const { data: res } = await this.$http.get('categories/73/attributes', { params: { sel: 'only' } })
        if (res.meta.status !== 200) return this.$message.error('')
        res.data.forEach(item => {
          item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split('')
        })
        this.onlyTableData = res.data
        console.log('', this.onlyTableData)
      }
    },
    // 
    handlePreview (file) {
      // console.log(file)
      // url
      this.previewPath = file.response.data.url
      this.previewVisible = true
    },
    // 
    handleRemove (file) {
      // console.log(file)
      const fileTmepPath = file.response.data.tmp_path
      // pics
      const index = this.addForm.pics.findIndex((value, index, arr) => {
        return value.pic === fileTmepPath
      })
      // 
      this.addForm.pics.splice(index, 1)
      console.log(this.addForm)
    },
    // 
    handleSuccess (response) {
      console.log(response)
      // 
      const picInfo = { pic: response.data.tmp_path }
      // pushpics
      this.addForm.pics.push(picInfo)
      console.log(this.addForm)
    },
    // 
    handleClose () {
      this.previewVisible = false
    },
    // 
    add () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return this.$message.error('')
        }
        // 
        // this.addForm.goods_catv-modelthis.addForm
        const form = _.cloneDeep(this.addForm)
        // API
        form.goods_cat = form.goods_cat.join(',')

        this.manyTableData.forEach(item => {
          const attr = { attr_id: item.attr_id, attr_value: item.attr_vals.join(' ') }
          this.addForm.attrs.push(attr)
        })

        this.onlyTableData.forEach(item => {
          const attr = { attr_id: item.attr_id, attr_value: item.attr_vals.join(' ') }
          this.addForm.attrs.push(attr)
        })
        form.attrs = this.addForm.attrs
        // 
        const { data: res } = await this.$http.post('goods', form)
        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        // 
        this.$router.push('/goods')
      })
    },
    // 
    deepClone (target) {
      // 
      let result
      // 
      if (typeof target === 'object') {
        // 
        if (Array.isArray(target)) {
          // resulttarget
          result = []
          target.forEach(item => {
            // 
            result.push(this.deepClone(item))
          })
          // null
        } else if (target === null) {
          result = null
          // RegExp 
        } else if (target.constructor === RegExp) {
          result = RegExp
        } else {
          // for in 
          result = {}
          for (const key in target) {
            result[key] = this.deepClone(target.key)
          }
        }
      } else {
        // 
        result = target
      }
      // 
      return result
    }
  },
  computed: {
    cateID () {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
.el-checkbox {
  margin: 0 10px 0 0;
}
.previewImg {
  width: 100%;
}
.btnAdd {
  margin-top: 15px;
}
</style>
