<template>
    <div>
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <!--  -->
            <el-alert
                title=""
                type="warning" :closable="false" show-icon>
            </el-alert>

            <!--  -->
            <el-row class="cat_opt">
                <el-col>
                    <span></span>
                    <!--     -->
                    <el-cascader
                    v-model="selectedCateKeys"
                    :options="cateList"
                    :props="{ expandTrigger: 'hover', ...cateProps }"
                    @change="handleChange">
                    </el-cascader>
                </el-col>
            </el-row>
            <!-- tab -->
            <el-tabs v-model="activeName" @tab-click="handleTabClick">
                <el-tab-pane label="" name="many">
                    <!--  -->
                    <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true"></el-button>
                    <!--  -->
                    <el-table :data="manyTabData" border stripe>
                        <!--  -->
                        <el-table-column type="expand">
                          <template slot-scope="scope">
                            <!-- tag -->
                            <el-tag :key="i" v-for="(item, i) in scope.row.attr_vals" closable @close='handleClose(i,scope.row)'>{{item}}</el-tag>
                            <!--  -->
                            <el-input
                            class="input-new-tag"
                            v-if="scope.row.inputVisible"
                            v-model="scope.row.inputValue"
                            ref="saveTagInput"
                            size="small"
                            @keyup.enter.native="handleInputConfirm(scope.row)"
                            @blur="handleInputConfirm(scope.row)"
                          >
                          </el-input>
                          <!--  -->
                          <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                          </template>
                        </el-table-column>
                        <!--  -->
                        <el-table-column type="index"></el-table-column>
                        <el-table-column label="" prop="attr_name"></el-table-column>
                        <el-table-column label="">
                            <template slot-scope="scope">
                                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.attr_id)"></el-button>
                                <el-button type="danger" icon="el-icon-delete" size="mini" @click="removePatams(scope.row.attr_id)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane label="" name="only">
                    <!--  -->
                    <el-button type="primary" size="mini" :disabled="isBtnDisabled" @click="addDialogVisible = true"></el-button>
                    <!--  -->
                    <el-table :data="onlyTabData" border stripe>
                        <!--  -->
                        <el-table-column type="expand"></el-table-column>
                        <!--  -->
                        <el-table-column type="expand">
                          <template slot-scope="scope">
                            <!-- tag -->
                            <el-tag :key="i" v-for="(item, i) in scope.row.attr_vals" closable @close='handleClose(i,scope.row)'>{{item}}</el-tag>
                            <!--  -->
                            <el-input
                            class="input-new-tag"
                            v-if="scope.row.inputVisible"
                            v-model="scope.row.inputValue"
                            ref="saveTagInput"
                            size="small"
                            @keyup.enter.native="handleInputConfirm(scope.row)"
                            @blur="handleInputConfirm(scope.row)"
                          >
                          </el-input>
                          <!--  -->
                          <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>
                          </template>
                        </el-table-column>
                        <el-table-column label=" " prop="attr_name"></el-table-column>
                        <el-table-column label="">
                            <template slot-scope="scope">
                                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.attr_id)"></el-button>
                                <el-button type="danger" icon="el-icon-delete" size="mini" @click="removePatams(scope.row.attr_id)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
            </el-tabs>
        </el-card>
        <!--  -->
        <el-dialog
        :title="'' + dialogTitle"
        :visible.sync="addDialogVisible"
        width="30%" @close='addDialogClosed'>
        <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
          <el-form-item :label="dialogTitle" prop="attr_name">
            <el-input v-model="addForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false"> </el-button>
            <el-button type="primary" @click="addParams"> </el-button>
        </span>
        </el-dialog>
        <!--  -->
         <el-dialog
        :title="'' + dialogTitle"
        :visible.sync="editDialogVisible"
        width="30%" @close='editDialogClosed'>
        <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
          <el-form-item :label="dialogTitle" prop="attr_name">
            <el-input v-model="editForm.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="editDialogVisible = false"> </el-button>
            <el-button type="primary" @click="editParams"> </el-button>
        </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
  data () {
    return {
      cateList: [],
      //   
      cateProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      //   
      selectedCateKeys: [],
      // 
      activeName: 'many',
      //   
      manyTabData: [],
      //   
      onlyTabData: [],
      // 
      addDialogVisible: false,
      // 
      addForm: {
        attr_name: ''
      },
      // 
      addFormRules: {
        attr_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      editDialogVisible: false,
      editForm: {
        attr_name: ''
      },
      // 
      editFormRules: {
        attr_name: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created () {
    this.getAllcateList()
  },
  methods: {
    //  
    async getAllcateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) return this.$message.error('')
      this.cateList = res.data
      console.log(this.cateList)
    },
    // 
    handleChange () {
      this.getParamsData()
    },
    async getParamsData () {
      // 
      if (this.selectedCateKeys.length !== 3) {
        this.selectedCateKeys = []
        this.manyTabData = []
        this.onlyTabData = []
      } else {
        console.log(this.selectedCateKeys)
        // ID
        const { data: res } = await this.$http.get(`categories/${this.threeCateID}/attributes`, { params: { sel: this.activeName } })
        if (res.meta.status !== 200) return this.$message.error('')
        console.log(res.data)
        // 
        res.data.froEach(item => {
          item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        })
        // const arr = res.data.map(item => {
        //   item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        //   // 
        //   item.inputVisible = false
        //   // 
        //   item.inputValue = ''
        //   return item
        // })
        // this.manyTabData = arr
        if (this.activeName === 'many') {
          this.manyTabData = [{ attr_name: '' }]
        } else {
          this.onlyTabData = [{ attr_name: '' }]
        }
      }
    },
    // tab
    handleTabClick () {
      console.log(this.activeName)
    },
    // 
    addDialogClosed () {
      // 
      this.$refs.addFormRef.resetFields()
    },
    // 
    addParams () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post(`categories/${this.threeCateID}/attributes`, { ...this.addForm, attr_sel: this.activeName })
        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        this.addDialogVisible = false
        this.getParamsData()
      })
    },
    // 
    async showEditDialog (attrID) {
      // 
      const { data: res } = await this.$http.get(`categories/${this.threeCateID}/attributes/${attrID}`, { params: { attr_sel: this.activeName } })
      if (res.meta.status !== 200) return this.$message.error('')
      this.addForm = res.data
      this.editDialogVisible = true
    },
    // 
    editDialogClosed () {
      // 
      this.$refs.addFormRef.resetFields()
    },
    // 
    editParams () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.put(`categories/${this.threeCateID}/attributes/${this.editForm.attr_id}`, { attr_name: this.editForm.attr_name, attr_sel: this.activeName })
        if (res.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        this.getParamsData()
        this.editDialogVisible = false
      })
    },
    // ID
    async removePatams (attrID) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') return this.$message.info('')
      // 
      const { data: res } = await this.$http.deleta(`categories/${this.threeCateID}/attributes/${attrID}`)
      if (res.meta.status !== 200) return this.$message.error('')
      // 
      this.$message.success('')
      this.getParamsData()
    },
    // enter
    async handleInputConfirm (row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      // return,
      row.attr_vals.push(row.inputValue.trim())
      // 
      this.saveAttrValue(row)
      row.inputValue = ''
      row.inputVisible = false
    },
    // attr_valsb
    async saveAttrValue (row) {
      // 
      const { data: res } = await this.$http.put(`categories/${this.threeCateID}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
    },
    // 
    showInput (row) {
      this.inputVisible = row.inputVisible
      // 
      this.$nextTick(() => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    // 
    handleClose (index, row) {
      row.attr_vals.splice(index, 1)
      // 
      this.saveAttrValue(row)
    }

  },
  computed: {
    //  
    isBtnDisabled () {
      if (this.selectedCateKeys.length !== 3) {
        return true
      }
      return false
    },
    // ID
    threeCateID () {
      if (this.selectedCateKeys.length === 3) {
        return this.selectedCateKeys[2]
      } else {
        return null
      }
    },
    // activeName
    dialogTitle () {
      if (this.activeName === 'many') {
        return ''
      } else {
        return ''
      }
    }
  }
}
</script>

<style lang="less" scoped>
.cat_opt {
    margin: 15px 0;
}
.el-tag {
  margin: 10px;
}
.input-new-tag {
  width: 120px;
}
</style>
